.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 40px;
  height: 40px;
}

@media screen and (min-width: 768px) {
  .square {
    width: 100px;
    height: 100px;
    font-size: 48px;
  }
}
